export default function SignInSide(props) {
  
  axios.post('http://localhost:3001/check_token',{token:localStorage.getItem('token')}).then((r)=>{
    console.log(r.data.data)
      if(r.data.data !== "-2")
            props.history.push("/") 
    })
  const { t } = useTranslation();
  const [open, setOpen] = React.useState(false);

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const classes = useStyles();
  const [values, setValues, ] = React.useState({
    amount: '',
    password: '',
    weight: '',
    weightRange: '',
    showPassword: false,
  });
  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };
  
  const handleMouseDownPassword = event => {
    event.preventDefault();
  };
  const handleChangePhone = (e)=>
  {
    const t = e.target.value
    for(var i=0;i<t.length;i++)
    {
      if(!Number.isInteger(parseInt(t[i])))
        {
          const v = t.replace(t[i],"")
          e.target.value = v
        }
    }
    if(e.target.value.length<10 || e.target.value.length >10)
      e.target.style.color= "red"
    else
      e.target.style.color= "green"
      form.phone = e.target.value
  };
  const send = ()=> {
    if(form.phone === null)
      form.phone = document.getElementById("Phone").value
    if(form.pswd === null)
      form.pswd = document.getElementById("password").value
    console.log(form.phone,form.pswd)
   if(form.phone!== null && form.pswd !== null )
    {
      if(form.phone.length === 10 && Number.isInteger(parseInt(form.phone)))
        {
              const data ={phone:form.phone ,pswd:form.pswd}
              axios.post('http://localhost:3001/login',data)
              .then((r)=>
              {
                form.phone = null;
                form.pswd = null
                  if(r.data.status === "success")
                  {
                      localStorage.setItem('token',r.data.token)
                      localStorage.setItem('type',r.data.type)
                      if(r.data.data == 1)
                        {
                          props.history.push("/map") 
                        }
                      else
                        {
                          props.history.push("/map_ven")
                        }
                  }
                  else
                  {
                    mssg =t('login.'+r.data.data)
                    setOpen(true);
                  }
              })
              .catch() 
        }
        else
        {
          mssg = t('login.PHONE')
          setOpen(true);
        }
      }
      else
      {
        mssg = t('login.DATA_MISSING')
        setOpen(true);
      }
    };
  const handleChangePswd =(e)=>
  {
    var n = 0;
    var u = 0;
    var l = 0;
    var Upper = /^[A-Z]+$/;
    var Lower = /^[a-z]+$/;
    var number = /^[0-9]+$/;
    for(var i = 0 ;i<e.target.value.length;i++)
    {
      if(e.target.value[i].match(Upper))
          u = 1
      if(e.target.value[i].match(Lower))
          l = 1
      if(e.target.value[i].match(number))
          n = 1
    }
    if(e.target.value.length<6 || e.target.value.length >20 || n === 0 || u === 0 || l === 0)
      e.target.style.color= "red"
    else
      e.target.style.color= "green"
      form.pswd = e.target.value
  };
if(localStorage.getItem('langue')  !== "ar")
  {
  return (
    <React.Fragment >
      <section className="page-title-banner " style={{backgroundColor:'#13c4a1',height:"4vh"}}>
                <div className="container ">
                    <div className="row">
                        <div className="col-lg-12">
                            <h2 >{t('login.BTN_LOGIN')}</h2>
                        </div>
                    </div>
                </div>

                <div className="shape1"><img src={require('../../images/shape1.png')} alt="img" /></div>
                <div className="shape2"><img src={require('../../images/shape2.png')} alt="img" /></div>
                <div className="shape3"><img src={require('../../images/shape3.png')} alt="img" /></div>
                <div className="shape6"><img src={require('../../images/shape6.png')} alt="img" /></div>
                <div className="shape8 rotateme"><img src={require('../../images/shape8.svg')} alt="shape" /></div>
                <div className="shape9"><img src={require('../../images/shape9.svg')} alt="shape" /></div>
            </section>
       
            <section className="login-area ptb-10 ">
                <div className="container">
                    <div className="row h-100 justify-content-center align-items-center">
                        <div className="col-lg-6 col-md-12">
                            <div className="login-image">
                                <img src={require('../../images/main-image.png')} alt="image" />
                            </div>
                        </div>

                        <div className="col-lg-6 col-md-12">
                            <div className="login-form">
                                <h3>{t('login.WELCOME')}</h3>
                                <p>{t('login.plz')}</p>
                                <form>
                                    <div className="row">
                                        <div className="col-lg-12">
                                            <div className="form-group">
                                            <ThemeProvider theme={theme}>
                                            <TextField
                                              margin="normal"
                                              fullWidth
                                              id="Phone"
                                              label={t('login.LABEL_PHONE')}
                                              name="Phone"
                                              autoComplete="Phone"
                                              variant="outlined"
                                              autoFocus
                                              onChange={handleChangePhone}
                                              InputProps={{
                                              
                                                startAdornment: <InputAdornment position="start"><PhoneOutlinedIcon /></InputAdornment>,
                                              }}
                                            />
                                            </ThemeProvider>
                                            </div>
                                        </div>
                                        <div className="col-lg-12">
                                            <div className="form-group">
                                            <ThemeProvider theme={theme}>
                                            <TextField
                                              variant="outlined"
                                              fullWidth
                                              name="password"
                                              value={values.Password}
                                              label={t('login.LABEL_PASS')}
                                              type="password"
                                              id="password"
                                              autoComplete="current-password"
                                              onChange={handleChangePswd}
                                              type={values.showPassword ? 'text' : 'password'}
                                              InputProps={{
                                                startAdornment: <InputAdornment position="start">
                                                <IconButton
                                                  aria-label="toggle password visibility"
                                                  onClick={handleClickShowPassword}
                                                  onMouseDown={handleMouseDownPassword}
                                                >
                                                {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                                </IconButton>
                                              </InputAdornment>,
                                              }}
                                            />
                                            </ThemeProvider>
                                            </div>
                                        </div>
                                        <div className="col-lg-12 text-right">
                                            <p className="forgot-password1">
                                                <Link href="/forgot">{t('login.F_MDP')}</Link>
                                            </p>
                                        </div>
                                        <div className="col-lg-12">
                                            <span className="btn1 btn-primary1" style={{float:'left',color:'white'}} onClick ={send}>{t('login.BTN_LOGIN')}!</span>
                                            </div>
                                            <div className="col-lg-12">
                                            <span>{t('login.TO_SIGNUP')}<Link href="/register">{t('register.BTN_SIGNUP')}!</Link></span>
                                        </div>
                                    </div>
                                </form>
                                <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                                        <Alert onClose={handleClose} severity="error" >
                                          {mssg}
                                        </Alert>
                                </Snackbar>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            </React.Fragment>
  );
      }
else
{
  return (
<React.Fragment >
<section className="page-title-banner " style={{backgroundColor:'#13c4a1',height:"4vh"}}>
                <div className="container ">
                    <div className="row">
                        <div className="col-lg-12">
                            <h2 >تسجيل الدخول</h2>
                        </div>
                    </div>
                </div>

                <div className="shape1"><img src={require('../../images/shape1.png')} alt="img" /></div>
                <div className="shape2"><img src={require('../../images/shape2.png')} alt="img" /></div>
                <div className="shape3"><img src={require('../../images/shape3.png')} alt="img" /></div>
                <div className="shape6"><img src={require('../../images/shape6.png')} alt="img" /></div>
                <div className="shape8 rotateme"><img src={require('../../images/shape8.svg')} alt="shape" /></div>
                <div className="shape9"><img src={require('../../images/shape9.svg')} alt="shape" /></div>
            </section>
       <section className="login-area ptb-10 ">
           <div className="container">
               <div className="row h-100 justify-content-center align-items-center">

                   <div className="col-lg-6 col-md-12">
                       <div className="login-form">
                           <h3 style={{textAlign:'right'}}>!مرحبا بعودتك</h3>
                           <p style={{textAlign:'right'}}>.يرجى تسجيل الدخول إلى حسابك</p>
                           <form>
                               <div className="row">
                                   <div className="col-lg-12">
                                       <div className="form-group">
                                       <ThemeProvider theme={theme}>
                                       <TextField
                                          margin="normal"
                                          fullWidth
                                          id="Phone"
                                          label="رقم الهاتف *"
                                          name="Phone"
                                          autoComplete="Phone"
                                          variant="outlined"
                                          onChange={handleChangePhone}
                                          className={classes.test}
                                          InputProps={{
                                          
                                          startAdornment: <InputAdornment position="start"><PhoneOutlinedIcon /></InputAdornment>,
                                          }}
                                          inputProps={{
                                          style: { textAlign: "right" },
                                          }}
                                      />
                                      </ThemeProvider>
                                       </div>
                                   </div>
                                   <div className="col-lg-12">
                                       <div className="form-group">
                                       <ThemeProvider theme={theme}>
                                       <TextField
                                          variant="outlined"
                                          fullWidth
                                          name="password"
                                          value={values.Password}
                                          label="كلمة السر *"
                                          type="password"
                                          id="password"
                                          onChange={handleChangePswd}
                                          autoComplete="current-password"
                                          type={values.showPassword ? 'text' : 'password'}
                                          InputProps={{
                                          startAdornment: <InputAdornment position="start">
                                          <IconButton
                                              aria-label="toggle password visibility"
                                              onClick={handleClickShowPassword}
                                              onMouseDown={handleMouseDownPassword}
                                          >
                                          {values.showPassword ? <Visibility /> : <VisibilityOff />}
                                          </IconButton>
                                          </InputAdornment>,
                                          }}
                                          inputProps={{
                                          style: { textAlign: "right" },
                                          }}
                                      />
                                      </ThemeProvider>
                                       </div>
                                   </div>
                                   <div className="col-lg-12 text-left">
                                       <p className="forgot-password1">
                                           <Link href="/forgot"> نسيت كلمة السر؟</Link>
                                       </p>
                                   </div>
                                   <div className="col-lg-12">
                                       <span  className="btn1 btn-primary1" style={{float:'right',color:'white'}} onClick ={send}>!تسجيل الدخول</span><br />
                                      </div>
                                      <div className="col-lg-12">
                                       <span className="text-right" style={{float:'right'}}>مستخدم جديد؟ <Link href="/register"> إنشاء حساب</Link></span>
                                   </div>
                               </div>
                           </form>
                           <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
                                        <Alert onClose={handleClose} severity="error" >
                                          {mssg}
                                        </Alert>
                            </Snackbar>
                       </div>
                   </div>
                   <div className="col-lg-6 col-md-12">
                       <div className="login-image">
                           <img src={require('../../images/main-image.png')} alt="image" />
                       </div>
                   </div>
               </div>
           </div>
       </section>
       </React.Fragment>
 );
}
}