import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import Lightbox from 'react-image-lightbox';
import dynamic from 'next/dynamic';
const OwlCarousel = dynamic(import('react-owl-carousel3'));

const options = { 
    autoplay: true,
    nav: true,
    loop:true,
    mouseDrag: true,
    autoplayHoverPause: true,
    responsiveClass: true,
    dots: false,
    navText: [
        "<i class='icofont-bubble-left'></i>",
        "<i class='icofont-bubble-right'></i>"
    ],
    responsive:{
        0:{
            items:1,
        },
        768:{
            items:2,
        },
        1200:{
            items:4,
        }
    }
}

const images = [
    require('../../images/work-img1.png'),
    require('../../images/work-img2.png'),
    require('../../images/work-img3.png'),
    require('../../images/work-img4.png'),
    require('../../images/work-img5.png'),
    require('../../images/work-img6.png')
]

class Works extends Component {

    state = {
        display:false,
        photoIndex: 0,
        isOpenImage: false,
    }

    componentDidMount(){ 
        this.setState({ display: true }) 
    }

    render() {
        const { photoIndex, isOpenImage } = this.state;
        return (
            <React.Fragment>
            <section className="project-area ptb-100">
                <div className="container">
                    <div className="section-title">
                        <h2> نماذج بعض المنتجات المحلية  </h2>
                        <div className="bar"></div>
                        <p> سيمكنكم من اكتشاف منتوجات مختلفة تعبر على مهارة واحترافية الأيدي العاملة المحلية، وسنطلعكم قريبا على مزايا وفوائد هاته المنتوجات لكي تتعرفوا </p>
                    </div>
                </div>

                <div className="row m-0">
                    {this.state.display ? <OwlCarousel 
                        className="project-slides owl-carousel owl-theme"
                        {...options}
                    >
                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img1.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span> قفطان</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية لباس المرأة</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img2.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span>فخار</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية الخزف الأصيل</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img3.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span>فخار</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية الخزف الأصيل</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>

                        {isOpenImage && (
                            <Lightbox
                                mainSrc={images[photoIndex]}
                                nextSrc={images[(photoIndex + 1) % images.length]}
                                prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                                onCloseRequest={() => this.setState({ isOpenImage: false })}
                                onMovePrevRequest={() =>
                                this.setState({
                                    photoIndex: (photoIndex + images.length - 1) % images.length,
                                })
                                }
                                onMoveNextRequest={() =>
                                    this.setState({
                                        photoIndex: (photoIndex + 1) % images.length,
                                    })
                                }
                            />
                        )}

                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img4.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span>زرابي</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية ولاد إيديك</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>

                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img5.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span>زرابي</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية ولاد إيديك</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>
                        
                        <div className="col-lg-12">
                            <div className="single-project">
                                <div className="project-image">
                                    <img src={require('../../images/work-img6.png')} alt="work" />

                                    <NavLink to="#">
                                        <a 
                                            className="popup-btn"
                                            onClick={e => {e.preventDefault(); this.setState({ isOpenImage: true })}}
                                        >
                                            <i className="icofont-plus"></i>
                                        </a>
                                    </NavLink>
                                </div>

                                <div className="project-content">
                                    <span> قفطان</span>
                                    <h3>
                                        <NavLink to="#">
                                            <a>تعاونية لباس المرأة</a>
                                        </NavLink>
                                    </h3>
                                </div>
                            </div>
                        </div>
                    </OwlCarousel> : ''}
                </div>
                <canvas id="canvas"></canvas>
            </section>
            </React.Fragment>
        );
    }
}

export default Works;
