import React,{useEffect,useState} from 'react';
import axios from "axios";
import { ThemeProvider,createMuiTheme } from '@material-ui/core/styles';
import {  withStyles} from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import Card from '@material-ui/core/Card';
import Grid from '@material-ui/core/Grid';
import AppBar from '@material-ui/core/AppBar';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { useTranslation } from 'react-i18next';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import SearchIcon from '@material-ui/icons/Search';
import { ToastContainer, toast } from 'react-toastify';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import HighlightOffIcon from '@material-ui/icons/Close';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import MuiAlert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import 'react-toastify/dist/ReactToastify.css';
import './index.css';
import './home.css';
import a4c from '../../images/a4c.png'
//var len= 0
var quan_acheter =0;
var produit={
    quan:null,
    cat:null,
    prix:null,
    phone:null,
    descri : null,
    len:0,
    id:null
}
var status = 'error'
var msg = null
function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const NavBar = withStyles(theme => ({
    root: {
      backgroundColor: "#F6F6F6",
      color: "black"
    },
  }))(AppBar);
var tableau=[];


var getCategorie=[];

var search={
    cat:null
}
const theme = createMuiTheme({
    palette: {
      primary: green,
    },
  });
 export default  function Home(props){
    if(!props || !props.location || !props.location.state || !props.location.state.id_user || !props.location.state.name || !props.location.state.descri)
        props.history.push('/map')
    const [data, setData] = useState({ hits: [] });
    const [tn, setDatan] = useState({ n: false ,len:null});
    
   useEffect(() => {
    const fetchData = async () => {
      if(props && props.location && props.location.state && props.location.state.id_user )
        {const result = await axios.post('https://agrisoo9.um6p.ma/api/get_myAnn',{id:props.location.state.id_user});
        if(localStorage.getItem('langue')  == 'fr')
        {
          for(var i = 0;i <result.data.length ;i++)
              { 
                if(result.data[i].name_categorie === "Pottery")
                  result.data[i].name_categorie = "Poterie"
                if(result.data[i].name_categorie === "Spices")
                  result.data[i].name_categorie = "Épices"
                if(result.data[i].name_categorie === "Traditional industry")
                  result.data[i].name_categorie = "Industrie traditionnelle"
                if(result.data[i].name_categorie === "Agricultural products")
                  result.data[i].name_categorie = "Production agricole"
                if(result.data[i].name_categorie === "Natural products")
                  result.data[i].name_categorie = "Produits naturels" 
                if(result.data[i].name_categorie === "Confectionery")
                  result.data[i].name_categorie = "Confiserie"
              }
        }
        if(localStorage.getItem('langue')  == 'ar')
        {
            for(var i = 0;i <result.data.length ;i++)
                { 
                  if(result.data[i].name_categorie === "Pottery")
                    result.data[i].name_categorie ="الفخار"
                  if(result.data[i].name_categorie === "Spices")
                    result.data[i].name_categorie = "التوابل"
                  if(result.data[i].name_categorie === "Traditional industry")
                    result.data[i].name_categorie = "الصناعة التقليدية"
                  if(result.data[i].name_categorie === "Agricultural products")
                    result.data[i].name_categorie = "المنتجات الفلاحية"
                  if(result.data[i].name_categorie === "Natural products")
                    result.data[i].name_categorie = "المنتجات الطبيعية" 
                  if(result.data[i].name_categorie === "Confectionery")
                    result.data[i].name_categorie = "الحلويات"
                }
          }
        setData({hits:result.data});}
        };
    fetchData();
  }, []);
  const [open, setOpen] = React.useState(false);
  const { t } = useTranslation();
  const handleChangeQuantity = event =>
  { 
    quan_acheter = event.target.value
  };
const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };
  axios.post('https://agrisoo9.um6p.ma/api/getCatergorie').then((r)=>
  {
      if(r.data)
      {
        if(localStorage.getItem('langue')  == 'en')
        {
          for(var i = 0;i <r.data.length ;i++)
          {
            getCategorie[i] = <MenuItem  key = {i} value = {r.data[i].id_categorie}> {r.data[i].name_categorie}</MenuItem>
          }
        }
        if(localStorage.getItem('langue')  == 'fr')
        {
            for(var i = 0;i <r.data.length ;i++)
                { 
                  if(r.data[i].name_categorie === "Pottery")
                    r.data[i].name_categorie = "Poterie"
                  if(r.data[i].name_categorie === "Spices")
                      r.data[i].name_categorie = "Épices"
                  if(r.data[i].name_categorie === "Traditional industry")
                      r.data[i].name_categorie = "Industrie traditionnelle"
                  if(r.data[i].name_categorie === "Agricultural products")
                      r.data[i].name_categorie = "Production agricole"
                  if(r.data[i].name_categorie === "Natural products")
                    r.data[i].name_categorie = "Produits naturels" 
                  if(r.data[i].name_categorie === "Confectionery")
                    r.data[i].name_categorie = "Confiserie"
                    getCategorie[i] = <MenuItem  key = {i} value = {r.data[i].id_categorie}> {r.data[i].name_categorie}</MenuItem>
                }
        }
        if(localStorage.getItem('langue')  == 'ar')
        {
          for(var i = 0;i <r.data.length ;i++)
          { if(r.data[i].name_categorie === "Pottery")
                r.data[i].name_categorie ="الفخار"
            if(r.data[i].name_categorie === "Spices")
                r.data[i].name_categorie = "التوابل"
            if(r.data[i].name_categorie === "Traditional industry")
              r.data[i].name_categorie = "الصناعة التقليدية"
            if(r.data[i].name_categorie === "Agricultural products")
                r.data[i].name_categorie = "المنتجات الفلاحية"
            if(r.data[i].name_categorie === "Natural products")
                r.data[i].name_categorie = "المنتجات الطبيعية" 
            if(r.data[i].name_categorie === "Confectionery")
                r.data[i].name_categorie = "الحلويات"
            getCategorie[i] = <MenuItem  key = {i} value = {r.data[i].id_categorie}> {r.data[i].name_categorie}</MenuItem>
          }
        }
      }
  }) 
  const handleChangeCategory =(event)=>
  {
    search.cat = event.target.value
  }

  const recherche = ()=>
  {
      if(search.cat)
      {
        axios.post("https://agrisoo9.um6p.ma/api/search",{category:search.cat,id:props.location.state.id_user})
        .then((r)=>{
            setData({hits:r.data});
        })
      }
  }
  const handleAddToCartFromView = ()=>
  {
    if(quan_acheter == 0)
      {
        msg =  t('annonce.ERR_QUANTITY')
        status = 'error'
         setOpen(true);
      }
  else 
    { 
      if(quan_acheter > produit.quan)
      {
        msg =  t('annonce.indispo')
        status = 'error'
         setOpen(true);
      }
      else
      {
        axios.post('https://agrisoo9.um6p.ma/api/add_cart',{token:localStorage.getItem('token'),id:produit.id,quantite:quan_acheter})
        .then((r)=>{
          if(r.data.status === "success")
          {
              msg = t('home.done')
              status = 'success'
              setOpen(true);
          }
          else
          {
              msg =  t('home.'+r.data.data)
              status = 'error'
              setOpen(true);

          }
        })
      }
    }
  };
  const info=(id,q,p,c,ph,d)=>
  {
    produit.len = 0
    produit.cat = c;
    produit.quan = q;
    produit.prix = p;
    produit.phone = ph;
    produit.descri = d;
    produit.id = id
    axios.post("https://agrisoo9.um6p.ma/api/get_ann",{id:id})
    .then((r)=>{
         tableau =r.data
         console.log(produit.len)
        if(tn.n === false)
         setDatan({n:true});
        else
         setDatan({n:false});
    })
  }
  const close =()=>
  {
    setDatan({n:false});
  }
  const next =()=>
  {
    produit.len++

    if(produit.len >= tableau.length)
    produit.len = 0;
  }
  const prec =()=>
  {
    produit.len--;
      
    if(produit.len < 0)
       produit.len = tableau.length - 1;
       
  }
    return(
        <div style={{marginTop:"7%"}}>
          <div className="mt-5" style={{height:"20%"}}>
            <h1 className="text-center font-weight-bold text-uppercase">{props.location.state.name}</h1>
            <p className="text-center font-weight-bold">{props.location.state.descri}</p>
            <img src={a4c} style={{width : "10%", height: "10%", marginLeft: "45%"}} />
          </div>
            <div className="bar">
                <NavBar position="static">
                    <Box display="flex"  style={{padding: "1% 3% 1% 1%" }}>
                        <ThemeProvider theme={theme}>
                            <FormControl className="arsearch mt-2 mr-1 pl-3 col-3">
                                <SearchIcon onClick={recherche} fontSize="large" className="icon" />
                            </FormControl>
                            <FormControl variant="outlined"  className="formcateg mr-5 col-8">
                                <InputLabel id="demo-simple-select-filled-label"> {t('home.CATEGO')}</InputLabel>
                                <Select
                                label ={t('home.CATEGO')}
                                labelId="demo-simple-select-filled-label"
                                id="demo-simple-select-filled"
                                onChange={handleChangeCategory}
                                >
                                    <MenuItem  disabled> {t('home.CATEGO')}</MenuItem>
                                    {getCategorie}
                                </Select>
                            </FormControl>
                        </ThemeProvider>
                    </Box>
                </NavBar>
            </div>
            
            <Grid container justify="center" className="mt-4">
                {data.hits && data.hits.map((element,index)=>(
                    <React.Fragment key={index} >
                    <Card  className="card" onClick={()=>{info(element.id_annonce,element.quantity,element.prix,element.name_categorie,element.phone,element.description)}}>
                    <div className="container">
                        <img
                            className="media"
                            src={`https://agrisoo9.um6p.ma/api/images/${element.name_images}`}
                        />
                        <div className="overlay" >
                            <div >
                                <Typography className="text">
                                    {element.quantity} {t('home.KG')}
                                </Typography>
                                <Typography className="text1">
                                {element.prix} {t('home.PRICE')}
                                </Typography>
                            </div>
                            <button className="buyButton"> {t('home.BUY')}</button>
                        </div>
                    </div>
                    </Card></React.Fragment>
                ))}          
            </Grid>
            {tn.n === true && tableau && <div className="modal  productQuickView show" style={{paddingRight: '16px', display: 'block'}}>
            <ToastContainer />
                <div className="modal-dialog modal-dialog-centered" role="document" style={{top: '10%'}}>
                    <div className="modal-content">
                        <button type="button" onClick={close} className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true"><HighlightOffIcon/></span>
                        </button>
                        <div className="row align-items-center">
                            <div className="col-lg-6 col-md-6">
                                <div className="productQuickView-image imgs">
                                    <ArrowBackIosIcon className="prec" onClick={prec}/>
                                    <img src={`https://agrisoo9.um6p.ma/api/images/${tableau[produit.len].name_images}`} alt="image" /> 
                                    <ArrowForwardIosIcon className="next" onClick={next}/>
                                </div>
                            </div>
                            <div className="col-lg-1 col-md-1 d-none d-lg-block d-md-block">
                                <div className="vl"></div>
                            </div>
                            <div className="col-xs-1 d-lg-none d-md-none d-xs-block my-4">
                                <div className="vl1"></div>
                            </div>
                            <div className="col-lg-5 col-md-5">
                                <div className="product-content">
                                    <h3>{produit.descri}</h3>
                                    <ul className="product-info">
                                        <li><span>{t('home.PRIX')}:</span>{produit.prix}{t('home.PRICE')} </li>
                                        <li><span>{t('home.QUAN')}:</span> {produit.quan}{t('home.KG')}</li>
                                        <li><span>{t('home.CAT')}:</span>{produit.cat}</li>
                                        <li><span>{t('home.PHO')}:</span>0{produit.phone}</li>
                                    </ul>
                                    <div className="row product-add-to-cart mt-5">
                                    <Grid item xs={5} className="col">
                                  <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    onChange={handleChangeQuantity}
                                    type="number"
                                    id="quantity"
                                    label={t('annonce.QUANTITY')}
                                    autoComplete="quantity"
                                    InputProps={{
                                      endAdornment: <InputAdornment position="end"></InputAdornment>,
                                    }}
                                  />
                              </Grid>
                                        <button 
                                            className="btn btn-primary col mt-2"
                                            onClick={handleAddToCartFromView}
                                        >
                                            <ShoppingCartIcon/> {t('home.ADD')}
                                        </button>
                                        
                                    </div>
                                    <Snackbar open={open} autoHideDuration={2000} onClose={handleClose}>
                                        <Alert onClose={handleClose}  severity={status}>
                                          {msg}
                                        </Alert>
                                    </Snackbar>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>}
        </div>
    )
} 