import React, { Component } from 'react';
import './responsive.css'
import './style.css'
class ComingSoon extends Component {

    state = {
        days: '',
        hours: '',
        minutes: '',
        seconds: ''
    };

    commingSoonTime = () => {
        let endTime = new Date("May 04, 2020 10:00:00 PDT");			
        let endTimeParse = (Date.parse(endTime)) / 1000;
        let now = new Date();
        let nowParse = (Date.parse(now) / 1000);
        let timeLeft = endTimeParse - nowParse;
        let days = Math.floor(timeLeft / 86400); 
        let hours = Math.floor((timeLeft - (days * 86400)) / 3600);
        let minutes = Math.floor((timeLeft - (days * 86400) - (hours * 3600 )) / 60);
        let seconds = Math.floor((timeLeft - (days * 86400) - (hours * 3600) - (minutes * 60)));
        if (hours < "10") { hours = "0" + hours; }
        if (minutes < "10") { minutes = "0" + minutes; }
        if (seconds < "10") { seconds = "0" + seconds; }
        this.setState({
            days, hours, minutes, seconds
        });
    }

    componentDidMount(){
        this.myInterval = setInterval(() => { 
            this.commingSoonTime();
        }, 1000);
    };

    componentWillUnmount() {
        clearInterval(this.myInterval);
    };

    onSubmit = e => {
        e.preventDefault();
    };

    render() {
        return (
            <section className="coming-soon">
                <div className="d-table">
                    <div className="d-table-cell">
                        <div className="container">
                            <div className="coming-soon-content">
                                <h1>نحن نطلق موقعنا الجديد</h1>
                                <p>العمل الجاد من أجل شيء لا يهمنا يسمى الإجهاد. العمل الجاد من أجل شيء نحبه يسمى الشغف</p>
                                <div id="timer">
                                    <div id="days">{this.state.days} <span>أيام</span></div>
                                    <div id="hours">{this.state.hours} <span>ساعات</span></div>
                                    <div id="minutes">{this.state.minutes} <span>الدقائق</span></div>
                                    <div id="seconds">{this.state.seconds} <span>الثواني</span></div>
                                </div>							  
                                <form onSubmit={this.onSubmit}>
                                    <input type="text" className="form-control" placeholder="اكتب البريد الالكتروني الخاص بك" />
                                    <button type="submit">الإشتراك</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default ComingSoon;
